require("dotenv").config();

export function authenticate() {
  var authenticated = true;
  fetch(process.env.REACT_APP_DOMAIN_URL + "/api/v1/user/authenticate", {
    credentials: "include",
  })
    .then((response) => {
      console.log(response);
      if (response.ok) {
        console.log("authenticated");
        authenticated = true;
      }
      if (!response.ok) {
        authenticated = false;
      }
    })
    .catch((error) => {
      authenticated = false;
    });
  return authenticated;
}
