import logo from "./logo.svg";
import "./App.css";
import TopBar from "./components/topbar/TopBar";
import TopBarNew from "./components/topbar/TopBarNew";
import Sidebar from "./components/sidebar/SideBar";
import SidebarNew from "./components/sidebar/SideBarNew";
import Home from "./pages/home/Home";
import OrderList from "./pages/orderlist/OrderList";
import RoastingList from "./pages/orderlist/roastinglist/RoastingList";
import CalculationsList from "./pages/savedCalculations/calculationsList";
import Calculation from "./pages/calculation/Calculation";
import RecipeList from "./pages/recipes/RecipeList";
import ProductList from "./pages/products/ProductList";
import Recipe from "./pages/recipe/Recipe";
import NewRecipe from "./pages/recipe/NewRecipe";
import NewProduct from "./pages/product/NewProduct";
import Analytics from "./pages/analytics/Analytics";
import Login from "./pages/login/login";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link,
  PrivateRoute,
  useLocation,
} from "react-router-dom";
import { useState, useEffect, useMemo } from "react";
import { useCookies } from "react-cookie";
import { UserContext } from "./UserContext";

function Main() {
  const [token, setToken] = useState();
  const [value, setValue] = useState(false);
  const [selectedUrl, setSelectedUrl] = useState("/home");
  const providerValue = useMemo(() => ({ value, setValue }), [value, setValue]);
  useEffect(() => {
    fetch("https://glitchhub.coffee/api/v1/user/authenticate", {
      credentials: "include",
    })
      .then((response) => {
        if (!response.ok) {
          setValue(false);
        } else {
          console.log(`Value ${value}`);
          setValue(true);
        }
      })
      .catch((error) => console.log(error));
  });
  if (!value || value == false) {
    return (
      <UserContext.Provider value={providerValue}>
        <Login />
      </UserContext.Provider>
    );
  }
  return (
    <>
      <UserContext.Provider value={providerValue}>
        <Router>
          <div className="mainContainer">
            <TopBarNew />
            <div className="mainBody">
              <SidebarNew />
              <Routes>
                <Route path="/" element={<Home />} />
                <Route path="/orders" element={<OrderList token={token} />} />
                <Route path="/recipes" element={<RecipeList />} />
                <Route path="/products" element={<ProductList />} />
                <Route path="/product/NewProduct" element={<NewProduct />} />
                <Route path="/recipe/:recipeID" element={<Recipe />} />
                <Route path="/recipe/NewRecipe" element={<NewRecipe />} />
                <Route path="/roastingList" element={<RoastingList />} />
                <Route path="/calculations" element={<CalculationsList />} />
                <Route
                  path="/calculation/:calculationID"
                  element={<Calculation />}
                />
                <Route path="/analytics" element={<Analytics />} />
              </Routes>
            </div>
          </div>
        </Router>
      </UserContext.Provider>
    </>
  );
  return (
    <div>
      <Router>
        <TopBar token={token} />
        {/* <TopBar token={token} /> */}
        <div className="container">
          <Sidebar />
          <UserContext.Provider value={providerValue}>
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/orders" element={<OrderList token={token} />} />
              <Route path="/recipes" element={<RecipeList />} />
              <Route path="/products" element={<ProductList />} />
              <Route path="/product/NewProduct" element={<NewProduct />} />
              <Route path="/recipe/:recipeID" element={<Recipe />} />
              <Route path="/recipe/NewRecipe" element={<NewRecipe />} />
              <Route path="/roastingList" element={<RoastingList />} />
              <Route path="/calculations" element={<CalculationsList />} />
              <Route
                path="/calculation/:calculationID"
                element={<Calculation />}
              />
              <Route path="/analytics" element={<Analytics />} />
            </Routes>
          </UserContext.Provider>
        </div>
      </Router>
    </div>
  );
}
const Navigation = () => (
  <nav>
    <Link to="/landing">Landing</Link>
    <Link to="/home">Home</Link>
    <Link to="/dashboard">Dashboard</Link>
    <Link to="/analytics">Analytics</Link>
    <Link to="/admin">Admin</Link>
  </nav>
);

function App() {
  return (
    <div>
      <Main />
    </div>
  );
}

export default App;
