import React from "react";
import Button from "@material-ui/core/Button";
import LogoutIcon from "@mui/icons-material/Logout";
import logo from "./logo.png";
import "./topbarnew.css";
import { useContext } from "react";
import { UserContext } from "../../UserContext";
export default function TopBarNew() {
  const { value, setValue } = useContext(UserContext);
  function logout() {
    fetch("https://glitchhub.coffee/api/v1/user/logout", {
      credentials: "include",
      method: "POST",
    })
      .then((response) => {
        setValue(false);
        if (!response.ok) {
          setValue(false);
        }
      })
      .catch((error) => {
        setValue(false);
      });
  }

  return (
    <>
      <div className="topBarContainer">
        <div className="topBarLogoContainer">
          <div className="topBarLogoContainer2">
            <img className="topBarLogo" src={logo} />
          </div>
        </div>
        <div className="pageNameContainer"></div>
        <div className="logOutContainer">
          <div className="logOutButton">
            <Button
              onClick={() => logout()}
              style={{
                backgroundColor: "#bd0000",
                color: "#FFFFFF",
                height: "50%",
                width: "100%",
              }}
              color=""
              startIcon={<LogoutIcon />}
              variant="contained"
            >
              Logout
            </Button>
          </div>
        </div>
      </div>
    </>
  );
}
